import React, { Component } from 'react'
import GameDisplay from './Front/GameDisplay/GameDisplay'
import GameBoard from './Back/gameBoard.js'
import Modal from './Modal/Modal'
import randPlayer from './Back/randPlayer'
import {connect} from 'react-redux'
import {setBoard} from './actions/actions'
import './style.css'

class App extends Component {
  constructor(){
    super();
    this.p1 = 5;
    this.p2 = null;
    this.p1Name = null;
    this.p2Name = null;
    this.turn = 0;
    this.state={board: this.initializeBoard()};
    this.turnCount= 1;
    this.p1Skill=3;
    this.p2Skill=3;
    this.modalOpen=true;
    this.handleStart = this.handleStart.bind(this);
   
  }
  componentDidMount(){
    console.log(this.state);
  
    //this.playGame();
  }
  initializeBoard(){
    let board = new GameBoard([
  [ {type:"checker", color: 'white', x:3 , y:
  -3  }, {type:"checker", color: 'white', x:4 , y:-3  } ],
  [ {type:"checker", color: 'white', x:2 , y:-2  }, {type:"checker", color: 'white', x:3 , y:-2  }, {type:"checker", color: 'white', x:4 , y:-2  }, null ],
  [ {type:"checker", color: 'white', x:1 , y:-1 }, {type:"checker", color: 'white', x:2 , y:-1 }, {type:"checker", color: 'white', x:3 , y:-1  }, null, null, {type:"checker", color: 'black', x: 6, y:-1  } ],
  [ {type:"checker", color: 'white', x:0 , y:0  }, {type:"checker", color: 'white', x:1 , y:0  },{type:"checker", color: 'white', x:2 , y:0  },    null,    null,    {type:"checker", color: 'black', x:5 , y:0  },    {type:"checker", color: 'black', x:6 , y:0  },    {type:"checker", color: 'black', x:7 , y:0  } ],
  [ {type:"checker", color: 'white', x:1, y: 1 }, null, null, {type:"checker", color: 'black', x:4 , y:1  }, {type:"checker", color: 'black', x:5 , y:1  }, {type:"checker", color: 'black', x:6 , y:1  } ],
  [ null, {type:"checker", color: 'black', x:3 , y:2  }, {type:"checker", color: 'black', x:4 , y:2  }, {type:"checker", color: 'black', x:5 , y:2  } ],
  [ {type:"checker", color: 'black', x:3 , y:3  }, {type:"checker", color: 'black', x:4 , y:3  } ] ]
  ,"white",0);
    return board;
  }
  nextValidMove(player){
    let newBoard = {};
    let move = player.getMove(this.state.board);
    newBoard = this.state.board.move(move[0],move[1]);
      if(typeof newBoard == 'string'){
        this.nextValidMove(player);
      }else{        
        this.setState({board: newBoard});
        return move;
      }
  }
  gameReapeater(player){
      if(this.state.board.gameOver()){
        console.log("CLEARED")
        return;
      }
      this.turnCount= this.turnCount +1;
      player = player.getName() == this.p1Name ? this.p2: this.p1;
      this.nextValidMove(player);
      setTimeout(()=>{
        this.gameReapeater(player);
      },100)     

  }
  playGame(){
    let player = this.p2;
    this.gameReapeater(player)

  }
  handleStart(ws,bs){
    console.log('hiii');
      this.p1 = new randPlayer("white",ws);
      this.p2 = new randPlayer("black",bs);
      this.p1Name = this.p1.getName();
      this.p2Name = this.p2.getName();
      this.modalOpen= false;
      this.playGame();
  }
  
  render() {
    let display = this.state.board.getClassicBoard();
  //  console.log(display);
    return (
      <div className="App">
       
          
        {this.modalOpen?<Modal handleStart={this.handleStart}/>:<span></span>}
        <GameDisplay display={display}/>
      </div>
    );
  }
}

export default App;



