class GameBoard {
	constructor(board, turn, depth){		
		this.turn =turn;
		this.board = board;
		this.depth = depth;		
	}
	
	/*
	putPiecesOn(board){
		let popBoard = board;
		for(let i = 0; i<this.pieces.length; i++){
			let piece = this.pieces[i];
			let x = piece.x;
			let y = piece.y;
			let index = this.posToIndex([x,y]);
			popBoard[index[1]][index[0]] = piece;
		}
		return popBoard;
	}





	/************************
	**Checkers & Getters **/

	getBoard(){
		return this.board;
	}
	getTurn(){
		return this.turn;
	}
	getDepth(){
		return this.depth;
	}

	isOnBoard(p){
		var cond1 = Math.abs(p[1])<=p[0];
		var cond2 = Math.abs(p[1]) <= -p[0]+7;
		return (cond1 && cond2);
	}
	allOnBoard(arPos){
		for(let i = 0; i<arPos.length;i++){
			if(!this.isOnBoard(arPos[i])){
				return false;
			}
		}
		return true;
	}
	getPiece(p){
		
		let p2 = this.posToIndex(p);
		if(this.board[p2[1]][p2[0]]){
			return this.board[p2[1]][p2[0]];
		}else{
			return false;
		}
	}
	color(p){
		let p2 = this.posToIndex(p);
		return this.board[p2[1]][p2[0]].color;
	}









	/* INDEX TRANSFORMS*/
	posToIndex(p){
		var y2 = p[1]+3;
		var x2 = p[0] - Math.abs(p[1]);
		return [x2,y2];
	}

	indexToPos(ind){
		let y = ind[1] -3;
		let x = ind[0] + Math.abs(y);
		return [x,y];
	}
	posToClassic(p){
		let x = p[0] -p[1];
		let y = p[0] +p[1];
		return [x,y];
	}
	ClassicToPos(p){
		let x = (p[0]+p[1])/2;
		let y = (p[1]-p[0])/2;
		return [x,y];
	}







	/*MOVE CHECKERS*/
	isSingle(p1,p2){
		if(typeof p2[0] == 'array' || p2.length !== 2 ){
			return false;
		}else  if(Math.abs(p1[0]-p2[0])==1 && (p1[1]-p2[1] == 0)){
			return true;
		}else if(Math.abs(p1[1]-p2[1])==1 && (p1[0]-p2[0] == 0)){
			return true;
		}else{
			return false;
		}
	}
	isDouble(p1,p2){
		if(Math.abs(p1[0]-p2[0])==2 && (p1[1]-p2[1] == 0)){
			return true;
		}else if(Math.abs(p1[1]-p2[1])==2 && (p1[0]-p2[0] == 0)){
			return true;
		}else{
			return false;
		}
	}
	areDoubles(p1, p2){
		if(typeof p2[0] == 'number' && this.isDouble(p1,p2)){
			return true;
		}else if(typeof p2[0] == 'number' && !this.isDouble(p1,p2)){
			return false;
		}
		if(!this.isDouble(p1,p2[0])){
			return false;
		}
		let i = 1;
		while(i < p2.length){
			if(!this.isDouble(p2[i-1],p2[i])){
				return false;
			}
			i++;
		}
		return true;

	}
	rightDirection(p1,p2){
		let piece =this.getPiece(p1);
		if(piece.type == "king"){return true;}
		let dir = (piece.color=="white"? 1:-1);
		let result = ((p2[1]-p1[1])+(p2[0]-p1[0]))*dir>0 ? true : false ;
		return result;
	}
	typePromoter(p1,p2){
		let piece =this.getPiece(p1);
		if(piece.type == "king"){return "king";}
		let dir = (piece.color=="white"? 1:-1);
		if(!this.isOnBoard([p2[0]+dir,p2[1]])&&!this.isOnBoard([p2[0],p2[1]+dir])){
			return "king";
		}else{
			return "checker";
		}
	}
	getMidPoint(startPos,endPos){
		let midpoint = [(startPos[0]+endPos[0])/2,(startPos[1]+endPos[1])/2 ]
		if(this.getPiece(midpoint)){
			return midpoint;
		}
		return false;
	}
	errorCode(array){
		let error = ""
		for (var i = 0; i < array.length; i++) {
			if(array[i] == false){
				error+=i;
			}
		}
		return error;
	}
	
	





	/**************/
	/* Move Logic */
	/*routes moves to simple move or Jump moves*/
	move(startPos,endPos){

		if(this.isSingle(startPos,endPos)){
			return this.boardToBoard(this.simpleMove(startPos,endPos));
		}else if( this.areDoubles(startPos, endPos)){
			return this.boardToBoard(this.jumper(startPos,endPos));
		}else{
			return "INVAID_MOVE0";
		}
	}

	boardToBoard(board){
		if(typeof board == "string"){
			return board;
		}
		let nextTurn = this.turn == "white"? "black":"white";
		let bigBoard = new GameBoard(board, nextTurn, this.depth +1);
		return bigBoard;
	}
	
	
	
	simpleMove(startPos,endPos){
		if(!this.isOnBoard(startPos)|| !this.isOnBoard(endPos) || !this.getPiece(startPos) || this.getPiece(endPos) || (this.color(startPos)!==this.turn) || !this.rightDirection(startPos,endPos)){
			return "noSir";
		}else{
			let oldPiece = this.getPiece(startPos);
			let newPiece = {
				type:this.typePromoter(startPos,endPos),
				color: oldPiece.color,
				x:endPos[0],
				y:endPos[1]
			};
			return this.makeBoard(startPos,endPos,newPiece);
		}
	}
	jumper(startPos,endPos){
		if(typeof endPos[0] =="number"){
			return this.jump(startPos,endPos);
		}else{
			let i = 1;
			let nxtBoard = this.jump(startPos,endPos[0]);
			if(typeof nxtBoard == "string"){
				return nxtBoard;
			}
			let nxtgameBoard = new GameBoard(nxtBoard,this.turn, this.depth); 
			while(i<endPos.length-1){
				nxtBoard = nxtgameBoard.jump(endPos[i-1],endPos[i]);
				nxtgameBoard = new GameBoard(nxtBoard,this.turn, this.depth);
				i++;
			}
			return nxtgameBoard.jump(endPos[endPos.length-2],endPos[endPos.length-1]);
		}
	}
	jump (startPos,endPos){
		if(!this.isOnBoard(startPos)|| !this.isOnBoard(endPos) || !this.getPiece(startPos) || this.getPiece(endPos) /*|| (this.color(startPos)!==this.turn)*/ || !this.rightDirection(startPos,endPos)){
			return "no Sir";
		}else if(!this.getMidPoint(startPos,endPos) || this.getPiece(this.getMidPoint(startPos,endPos)).color==this.getPiece(startPos).color){
			return "No enemy to jump";
		}else{
			let oldPiece = this.getPiece(startPos);
			let midpoint = this.getMidPoint(startPos,endPos);
			let newPiece = {
				type:this.typePromoter(startPos,endPos),
				color: oldPiece.color,
				x:endPos[0],
				y:endPos[1]
			};
			return this.makeBoard(startPos,endPos,newPiece,midpoint);

		}	
	}
	makeBoard(startPos,endPos,newPiece,midpoint){
		let outerAr = [];
		for(let i= 0; i<7; i++){
			let innerAr = [];
			//something
			for(let j=0; j<(8-Math.abs(6-2*i));j++){
				if((this.indexToPos([j,i])[0] == startPos[0])&&(this.indexToPos([j,i])[1] == startPos[1])){
					innerAr.push(null);
				}else if((this.indexToPos([j,i])[0] == endPos[0])&&(this.indexToPos([j,i])[1] == endPos[1])){
					innerAr.push(newPiece);
				}else if(midpoint &&(this.indexToPos([j,i])[0] == midpoint[0])&&(this.indexToPos([j,i])[1] == midpoint[1])){
					innerAr.push(null);
				}else{
					innerAr.push(this.board[i][j]);
				}
			}
			outerAr.push(innerAr);
		}
		return outerAr;
	}
	
	







	/*OUTSIDER TOOLS*/
	gameOver(){
		if(this.searchPieces({color:"white"}).length*this.searchPieces({color:"black"}).length){
			return false;
		}
		console.log('done')
			return this.board;
	}
	getClassicBoard(){
		let pieces = this.searchPieces();
		let outerAr = [];
		for (var i = 0; i < 8; i++) {
			let innerAr = [];
			for (var j = 0; j<8; j++) {
				innerAr.push(null);
			}
			outerAr.push(innerAr);
		} 

		let pos = [];
		for (var i = 0; i < pieces.length; i++) {
			pos = this.posToClassic([pieces[i].x,pieces[i].y]);
			console.log(pieces[i].x);
			let classicPiece = {
				type:pieces[i].type,
				color:pieces[i].color,
				x:pos[0],
				y:pos[1]
			}
			outerAr[pos[1]][pos[0]] = classicPiece;
		}
		return outerAr;
	}
	searchPieces(obj){
		let pieces = [];
		for(let i= 0; i<7; i++){	
			for(let j=0; j<(8-Math.abs(6-2*i));j++){
				let piece = this.getPiece(this.indexToPos([j,i]));
				let piecePasses = piece;
				for(const attr in obj){
					if(obj[attr] !== piece[attr]){
						piecePasses = false;
					}
				}
				if(piecePasses){
					pieces.push(piece);
				}
			}			
		}
		return pieces;
	}

	getAllMoves(){
		let pieces = this.searchPieces({color: this.turn});
		let moveArray = [];
		for (let i = 0; i < pieces.length; i++) {
			moveArray  = moveArray.concat(this.allMovesFrom(pieces[i]));
		}
		for (let i = 0; i < moveArray.length; i++) {
			if(moveArray[i][1].length == 1){
				moveArray[i][1] = moveArray[i][1][0];
			}
		}
		return moveArray;
	}
	allMovesFrom(p){
		return this.simplesFrom(p).concat(this.doublesFrom(p));
	}
	simplesFrom(p){
		let dir = p.color == 'white'? 1:-1;		
		let start = [p.x,p.y];
		let checks = [];
		let moves=[];
		checks.push([p.x+dir,p.y]);
		checks.push([p.x,p.y+dir]);
		if(p.type == 'king'){
			checks.push([p.x-dir,p.y]);
			checks.push([p.x,p.y-dir]);
		}
		for (var i = 0; i < checks.length; i++) {
			if(!this.isOnBoard(start)|| !this.isOnBoard(checks[i]) || !this.getPiece(start) || this.getPiece(checks[i]) || (this.color(start)!==this.turn) || !this.rightDirection(start,checks[i])){
			
			}else{
				moves.push([start,[checks[i]]]);
			}
		}
		return moves;
		
	}
	doublesFrom(p){
		let tree = this.doublesFromTree(p);
		let prelimMoves = this.movify(tree);
		let moves = []
		if(prelimMoves.length <= 1){
			return [];
		}else{
			for (var i = 1; i < prelimMoves.length; i++) {
				prelimMoves[i].shift();
				moves.push([[p.x,p.y], prelimMoves[i]]);
			}
			return moves;
		}

		
	}
	doublesFromTree(p){
		let dir = p.color == 'white'? 2:-2;
		let start = [p.x,p.y];
		let checks = [];
		let tree = {}
			tree.pos = start;
			tree.child0 =  null;
			tree.child1 =  null;
			tree.child2 =  null;
			tree.child3 =  null;
			
		checks.push([p.x+dir,p.y]);
		checks.push([p.x,p.y+dir]);
		if(p.type == "king"){
			checks.push([p.x-dir,p.y]);
			checks.push([p.x,p.y-dir]);
		}
		for (var i = 0; i < checks.length; i++) {
			 		if(!this.isOnBoard(start)|| !this.isOnBoard(checks[i]) || !this.getPiece(start) || this.getPiece(checks[i]) || (this.color(start)!==this.turn) || !this.rightDirection(start,checks[i])){
				
			}else if(!this.getMidPoint(start,checks[i]) || this.getPiece(this.getMidPoint(start,checks[i])).color==this.getPiece(start).color){
				
			}else{
				let nextBoard = new GameBoard(this.jump(start, checks[i]),this.turn, this.depth);
				let keey = "child" + i ;
				tree[keey]=(nextBoard.doublesFromTree(nextBoard.getPiece(checks[i])));
			}
		}
		return tree;		
	}
	movify(tree){
		let lMoveAr = [tree.pos];
		let moves = [[tree.pos]];
		let bMoveAr = [];
			
		if(tree.child0){
			bMoveAr = bMoveAr.concat(this.movify(tree.child0));
		}
		if(tree.child1){
			bMoveAr = bMoveAr.concat(this.movify(tree.child1));
		}
		if(tree.child2){
			bMoveAr = bMoveAr.concat(this.movify(tree.child2));
		}
		if(tree.child3){
			bMoveAr = bMoveAr.concat(this.movify(tree.child3));
		}/*
		if(bMoveAr.length == 1){
			 return [lMoveAr];
			 
		}*/
		for (var i = 0; i < bMoveAr.length; i++) {
			moves.push(lMoveAr.concat(bMoveAr[i]));
		}
		//console.log(tree.child0);
		return moves;
	}

		
}
export default GameBoard;


