import React from 'react'
import boardpng from '../../images/board.png'
import checker from '../../images/checker.png'

class GameDisplay extends React.Component {
	getIcon(x,i,j){
	    if(x == null){
	      return (<span></span>);      
	    }else{
	      return (
	      	<div className={"piece " + x.color+x.type} style={{top:22.6 + 3.9*j + .14*j*j + .015*j*j*j+ "%", left: (24.0  - 1.8*j) +  i*9.9/(1.4-(.057*j)) +'%', width:6+.5*j + '%',height:4+.5*j + '%' }}>
				
	      	</div>); 
	    }
	  }

	render(){
		const display =this.props.display
		return (
			<div className="game-display-container">

				{display.map((row,j)=>(
		            <span>
		              {row.map((x,i)=>(
		                
		                  this.getIcon(x,i,j)
		                 
		              ))}
		             </span>  
		          ))}

				<img src={boardpng} alt="" className="board-image"/>				
			</div>
		)
	}
}

export default GameDisplay


