import React from 'react';


class Modal extends React.Component {
	constructor(){
		super();
		this.state={ws:4, bs:4}
	}
	
	render(){
		let blackNode;
		return(
			<div className="modal-env">
				<div className="modal-inner-container">
					<div className="modal-title">Select AI Skill Level</div>
					<div className="slider-container">
						<div className="white-container">
							White
							<input type="range" min="1" max="4" class="slider" ref='wslide' onChange={()=>{this.setState({ws:this.refs.wslide.value})}}/>
							{this.state.ws}
						</div>
						<div className="black-container">
							Black
							<input type="range" min="1" max="4"  class="slider" ref='bslide' onChange={()=>{this.setState({bs:this.refs.bslide.value})}}/>
							{this.state.bs}
						</div>					
					</div>
					<div className="start-container"> 
						<div className="start-button"
							onClick={()=>{this.props.handleStart(this.state.ws,this.state.bs)}}
						>Start Game</div>
					</div>
				</div>
			</div>
		)
	}
}

export default Modal;


