class randPlayer {
	constructor(color,depth){
		this.color  = color;
		this.maxDepth = depth;
		this.counter = 0;
		this.minMaxTable={};
	}
	getMove(board){
		let moves= board.getAllMoves();
		//let moveInd= Math.floor(Math.random()*moves.length);
		let moveInd = this.chooseMove(moves, board);
		let move = moves[moveInd];
		console.log(move[0],move[1]);
		return [move[0],move[1]];


	}
	chooseMove(moves,board){
		let bestMove=0;
		let bestScore=-10000;
		let initDepth=board.getDepth();
		for(let i = 0; i < moves.length; i++){
			let score = this.minMax(moves[i],board,initDepth);
		//	console.log('1');
			if(score > bestScore){
				bestScore = score;
				bestMove = [i];
			}else if(score == bestScore){
				bestMove.push(i);
			}
		}
		this.counter = 0;
		let finalMove = bestMove[Math.floor(Math.random()*bestMove.length)]
		return finalMove;
	}
	minMax(move, board,initDepth){
		let nextBoard=board.move(move[0],move[1]);
		let location = "";

		for(let i= 0; i<7; i++){	
			for(let j=0; j<(8-Math.abs(6-2*i));j++){
				let piece = nextBoard.getPiece(nextBoard.indexToPos([j,i]));
				if(!piece){					
					location +="n";
				}else{
					let typLetter = piece.type == 'checker'? 'c':'k';
					let colLetter = piece.color == 'white'? 'w':'b';
					let keey = typLetter + colLetter;
					location+=keey;
				}
				
			}			
		}
		location +=nextBoard.getDepth() - initDepth;		
		
		this.counter++;
		
		
		if(nextBoard.gameOver() || nextBoard.getDepth()-initDepth > this.maxDepth){
			location+="eval";
			if(this.minMaxTable[location]){
				//console.log(location);
				return(this.minMaxTable[location]);
			}
			let score = this.evalScore(nextBoard,location);
			this.minMaxTable[location] = score;
			return score;
		}
		
		let moves = nextBoard.getAllMoves();		
		if(this.color == nextBoard.getTurn()){
			location+="max";
			if(this.minMaxTable[location]){
				//console.log(location);
				return(this.minMaxTable[location]);
			}
			let bestScore = -10000;
			for(let i = 0; i < moves.length; i++){
				let score = this.minMax(moves[i],nextBoard,initDepth);
				if(score>bestScore){
					bestScore = score;
				}
			}
			this.minMaxTable[location] = bestScore;
			return bestScore;
		}else{
			location+="min";
			if(this.minMaxTable[location]){
				//console.log(location);
				return(this.minMaxTable[location]);
			}
			let bestScore = 10000;
			for(let i = 0; i < moves.length; i++){
				let score = this.minMax(moves[i],nextBoard,initDepth);
				if(score<bestScore){
					bestScore = score;
				}
			}
			this.minMaxTable[location] = bestScore;
			return bestScore;
		}

	}
	evalScore(board,keey){
		let otherColor = this.color=="white"? "black":"white";
		let pieceCountDifScore = 15*(board.searchPieces({color: this.color, type:"king"}).length -
			board.searchPieces({color: otherColor, type:"king"}).length) + 
			10*(board.searchPieces({color: this.color, type:"checker"}).length - 
			board.searchPieces({color: otherColor, type:"checker"}).length);
		let mobilityScore = board.getAllMoves().length;
		if(board.searchPieces({color:this.color}).length == 0){
			return -9999;
		}
		if(board.searchPieces({color:otherColor}).length == 0){
			return 9999;
		}
		return  pieceCountDifScore + mobilityScore;
	}
	getName(){
		return "player"+this.color;
	}
	errorMessage(){
		return "oops";
	}
}
export default randPlayer


