const reducer =(state = {
		board: null,
		startGame:false
},action)=>{
	switch(action.type){
		case 'START_GAME':
			return {
				...state,
				startGame: true
			}
		case 'SET_BOARD':
			return{
				...state,
				board: action.board
			}
		default:
			return state;
	}
}

export default reducer;


